.timer {
  position: absolute;
  top: calc(50% - 150px);
  left: calc(50% - 150px);
  height: 300px;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 50%;

  background-image: linear-gradient(
    rgba(255, 255, 255, 0.3),
    rgba(0, 0, 0, 0.1) 50%
  );
  background-size: 100% 200%;
  animation: background_color 5s linear forwards;
}

.timerValue {
  color: #fff;
  font-size: 72px;
  margin-top: -10px;
  animation: timer_beat 1s linear infinite;
}

@keyframes background_color {
  100% {
    background-position: 0 -100%;
  }
}

@keyframes timer_beat {
  40%,
  80% {
    transform: none;
  }
  50% {
    transform: scale(1.125);
  }
}
